.button {
  width: 100%;
  height: 56px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: $nav--color;
  background-color: $color--white;
  border-radius: 40px;
  font-weight: 700;
  line-height: 27px;
  cursor: pointer;
  transition: all 0.3s ;

  &:hover {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.4);
  }

  &:active {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.64);
  }

  &__primary {
    margin: 0;
    font-size: 16px;
  }
}
