.compareBike {

  @include onTablet {
    grid-column: 2 / 6;
  }

  &__photo {
    margin-bottom: 32px;
    max-width: 100%;
    transition: transform 0.3s;
    display: flex;
    justify-content: center;

    &:hover {
      transform: scale(1.1);
    }

    @include onTablet {
      width: 324px;
      height: 256px;
    }

  }

  &__title {
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
    margin: 0 0 8px;
  }

  &__description {
    line-height: 140%;
    margin: 0 0 16px;
  }

  &__price {
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
    margin: 0;
  }
}
